<template>
    <div class="swiper-container" :id="id">
        <div class="swiper-wrapper">
            <slot>这是swiper 内容分发slot</slot>
        </div>
        <div class="swiper-pagination"></div><!--分页器 -->       
    </div>  
</template>

<script>
import Swiper from "swiper";
import "swiper/css/swiper.min.css";

//封装组件 props + slot
export default {
    props:{
        id:String,
        swiperoptions:{
            type:Object,
            default:{

            }
        }
    },
    mounted(){
        //swiper实例化
        let mySwiper = new Swiper("#"+this.id,this.swiperoptions);   
    },
    updated(){
        //异步数据
        let  mySwiper = new Swiper("#"+this.id,this.swiperoptions);
    }
}

</script>