import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//Vuex 分层
//单项数据流 state===> views ===> actions ===> stste

//state 所有组件的状态对象 驱动应用的数据源
//Vuex 使用单一状态树 用一个对象state来保存所有组件的状态

//getter附加属性 就是对state进行优化 派生(过滤 优化)

//mutations 修改状态的地方
//mutation非常类似于事件 每个mutation都有一个字符串的事件类型(type) 和一个回调函数(handler)
//mutation只能写 同步操作

//actions
//1.Action提交的是 mutation,而不是直接变更状态
//2.Action可以包含任意异步操作

//moudles 

//store 每一个Vuex 应用的核心就是store (仓库)

import {
  CHANGEWORD,
  CHANGECITY,
  COUNTDESC
} from "./types"
import {axios} from "@/utils/axios";

const store = new Vuex.Store({
  state:{
    count:1903,
    num:0,
    city:"Tailan-泰国",
    word:"hahahah",
    msg:"nz1903",
    yum:{
      players:[],
    },
    obj:{
      banner:[],
    }
  },
  mutations:{  //改变store中的状态的唯一途径就是显示地提交(commit) mutation
    changeNum(state){  //mutation type
      state.num++;
    },
    changeCount(state,payload){  //payload参数 组件提交过来的
      state.count += payload;
    },
    [COUNTDESC](state){ //每个回调函数都接受state作为第一个参数
      state.count--;
    },
    [CHANGECITY](state,payload){
      state.city = payload;
    },
    [CHANGEWORD]:function(state,payload){
      state.word = payload;
    },
    changeMsg(state,payload){
      state.msg = payload;
    },
    getPlayers(state,payload){
      //当需要在对象上添加新属性时
      //state.yum.players = payload;
      state.yum = {...state.yum,players:payload}
    },
    getBanner(state,payload){
      state.obj = {...state.obj,banner:payload}
    }
  },
  modules:{

  },
  actions:{//如果有异步请求 使用action
    changeMsg(context){  //action 一定有提交mutation操作
      axios.get("/vue")
      .then(res=>{
        context.commit("changeMsg",res.data.msg);
      })
    },
    getPlayers({commit}){  //context = {commit:fi=unction(){}} = {commit:commit} = {commit}
      axios.get("/yun/toplist/artist")
      .then(res=>{
        commit("getPlayers",res.data.list.artists);
      })
    },
    getBanner({commit}){
      axios.get("/vue/movie?limit=5")
      .then(res=>{
        commit("getBanner",res.data.result);
      })
    }
  },
  getters:{
    newCount(state){
      return state.count + 1000;
    }
  }
})



export default store;
