// eslint-disable

import Vue from 'vue'  //导入vue模块
import App from './App.vue'   //根组件
import router from './router'
import store from './store'
import './registerServiceWorker'

Vue.config.productionTip = false
//vue核心文件 启动项目

import "lib-flexible"; //淘宝适配必须引入的 引入js文件

import "@/styles/index.scss";  //引入全局的样式

import "@/utils/vant";  //按需加载
//引入Vant
//import Vant from "vant"
//import 'vant/lib/index/css';  //一次性导入所有的样式
//Vue.use(Vant)

import "@/utils/quan" //全局注册
import {axios} from "@/utils/axios"
Vue.prototype.$axios = axios;  //this.$axios = axios

new Vue({
  router,  //挂载路由 this.$router
  store,
  render: h => h(App)  //render Vue 渲染虚拟DOM的函数
}).$mount('#app')      //把虚拟DOM App载入到div#app里面


